.card-content {
  background-color: var(--background-color);
  border-radius: 10px;
  box-shadow: var(--card-box-shadow);
  padding: 0px;
  margin: 5% 15%;
  max-width: 80%;
}

.card-header {
  background-color: var(--primary-color);
  color: white;
  padding: 10px 20px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'GO MONO FOR POWERLINE';
}

.card-title {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  cursor: pointer;
}

.tig-title:hover,
.arabic-title:hover,
.english-title:hover {
  border-radius: 0.5em;
  border: 2px solid rgb(255, 255, 255);
  font-weight: bold;
  padding: 0.3em;
}

.tig-title.active,
.arabic-title.active,
.english-title.active {
  border-radius: 0.5em;
  border: 2px solid rgb(255, 255, 255);
  padding: 0.3em;
  font-weight: bold;
}

.horizontal-title {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}

.vertical-title {
  display: none;
}

.tig-title {
  font-size: 20px;
  font-family: 'Arial';
}

.arabic-title {
  font-size: 24px;
  font-family: 'Traditional Arabic', 'Arial', sans-serif;
  color: white;
  padding: 5px 10px;
  text-align: center;
}

.english-title {
  font-size: 20px;
  font-family: 'GO MONO FOR POWERLINE';
}

.active {
  font-weight: bold;
  border-bottom: 2px solid white; /* Indicate active title with underline */
}

.verse-block {
  border-bottom: 1px solid #ccc;
}

.verse-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em 1em;
  padding-bottom: 0em;
  margin: 0em 0em;
}

.arabic-verse-num {
  font-family: 'Traditional Arabic', 'Arial', sans-serif;
  font-size: 40px;
  color: var(--text-color);
  margin-right: 10%;
  z-index: 1;
}

.verse-number {
  color: var(--text-color);
  padding: 5px 10px;
  margin-left: 1em;
  font-size: 35px;
  z-index: 1;
  font-family: 'GO MONO FOR POWERLINE';
  text-align: right;
}

.arabic-text {
  font-family: 'amiri-quran';
  font-size: 20px;
  text-align: right;
}

.tigrinya-text {
  font-family: 'Yebse';
  font-size: 16px;
  display: flex;
}

.translation-text {
  font-family: 'GO MONO FOR POWERLINE';
  font-size: 14px;
  display: flex;
}

.tigrinya-text,
.translation-text {
  color: var(--text-color);
  text-align: left;
  padding-left: 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.verse-number-container {
  text-align: right; 
  padding: 0.5em 1em;
}

.btn-back {
  position: fixed;
  top: 7em;
  right: 1vw;
  padding: 0em;
  text-align: end;
}

@media (max-width: 576px) {
  .card-title {
    align-items: center;
    display: block;
  }
  .card-header {
    padding: 0em 0em;
    margin: 0em;
  }

  .vertical-title {
    display: flex !important;
    align-items: center;
  }

  .horizontal-title {
    display: block;
  }


  .card-content {
    max-width: 100%;
    margin: 5%;
  }

  .btn-back {
    display: none;
  }
}
